body {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 auto;
}

.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-size: 20px;
    background-color: #343434;
    padding: 10px;
}

.nav-links {
    display: -ms-flexbox;
    display: flex;
}

.logo {
    font-family: 'Teko', sans-serif;
    color: #ffffff;
    margin-left: 30px;
    font-size: 30px;
}

.link {
    margin: 8px 15px;
    text-decoration: none;
    color: #919191;
    outline: none;
    -webkit-transition: color 0.5s ease;
    -o-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.link:hover {
    color: #ffffff;
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
}

.project-list {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
}

.project-elem {
    display: inline-block;
    margin: 15px;
    cursor: pointer;
}

.project-elem:hover {
    -webkit-transform: scale(1.04);
        -ms-transform: scale(1.04);
            transform: scale(1.04);
}
  
.project-item-wrapper {
    display: block;
    text-align: center;
}
  
.project-item-img {
    border: 2px solid #000000;
    width: 250px;
    height: 250px;
}
  
.project-item-title {
    margin: 8px auto;
    font-family: 'Teko', sans-serif;
    font-size: 30px;
    color: #000000;
}

.intro-header {
    text-align: center;
    margin: 100px auto 0 auto;
    font-size: 40px;
    font-family: 'Teko', sans-serif;
    letter-spacing: 1.1px;
}

.about-me-wrapper {
    text-align: justify;
    letter-spacing: 0.7px;
    font-size: 23px;
    width: 500px;
    margin: 0 auto;
}

.projects-header {
    font-family: 'Teko', sans-serif;
    text-align: center;
    font-size: 40px;
}

.linkedin-img {
    width: 50px;
    height: 50px;
}

.contact-content-wrapper {
    display: center;
    text-align: center;
    font-family: 'Teko', sans-serif;
}

.contact-header {
    font-size: 40px;
    margin: 100px auto 0 auto;
    font-family: 'Teko', sans-serif;
    letter-spacing: 1.1px;
}

.contact-info {
    font-size: 27px;
    letter-spacing: 1.2px;
}

.splitter {
    width: 500px;
    height: 0.5px;
}


